.main__list--item {
    margin: 30px;
    border-radius: 10px;
    width: 270px;
    height: 370px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: white;
    text-align: center;
    &:hover {
        transform: translateY(0);
        box-shadow: 0 5px 25px 0px #0d0c14;
        .list__item--img{
            filter: grayscale(0%);
        }
        .list__item--title{
            color: gray;
        }
    }
}

.list__item--img{
    width: 200px;
    height: 200px;
    background-size: cover;
    background-repeat: no-repeat;
    filter: grayscale(100%);
    padding-bottom: 10px;
}

.list__item--title{
    color: black;
    text-decoration: none;
    margin-bottom: 20px;
    font-weight: 300;

}
